import React, { createRef, ChangeEvent } from "react";
import "./index.less";
import { Upload, Icon } from "antd";
import config from "../../api/config";

interface ImageUploadComponentProps {
  imageUrl?: string,
}
interface ImageUploadComponentState {
  loading: boolean
}
interface HTMLInputEvent extends Event {
  target: HTMLInputElement & EventTarget;
}
export default class ImageUploader extends React.Component<ImageUploadComponentProps, ImageUploadComponentState> {
  private input = createRef<HTMLInputElement>()
  constructor(props: ImageUploadComponentProps) {
    super(props);
    this.state = {
      loading: false,
    }
    // this.input = React.createRef();
  }
  public beforeUpload(): boolean {
    return true;
  }
  public handleChange() {
    //
  }
  public upload = () => {
    const node = this.input.current;
    if (node) {
      node.click();
    }
  }
  public uploading = (e: ChangeEvent<HTMLInputElement>) => {
    if (!e.target.files) {
      return
    }
    const file = e.target.files[0];
    const type = file.type;
    if (!/^image\/(png|jpg|jpeg)/.test(type)) {
      // 照片支持.jpg .jpeg .bmp .png .gif格式
      // this.imageError = "图片格式为jpg/png";
      // this.$toast({
      //   type: "error",
      //   message: "图片格式为jpg/png"
      // });
      return;
    }
    if (file.size > 1024 * 1024 * 8) {
      // 大小不超过8M
      return;
    }
    const reader = new FileReader();
    reader.readAsDataURL(file);
    const that = this;
    reader.onload = (event: ProgressEvent): any => {
      // that.imageSource = event.target.result;
      // const blob = that._parseBase64(reader.result);
      // that.base64image = blob;
      // that.uploadFile(file).then(res => {
      //   that.imageUrl = res.data.imgUrl;
      // })
    }
  }
  public render() {
    return (
      <div className="mf-uploader">
        { this.props.imageUrl ? (<div className="mf-uploader__image">
          <img src={this.props.imageUrl} alt="" onClick={this.upload}/>
          <a className="mf-uploader__image__reupload" onClick={this.upload}>重新上传</a>
        </div>) : (
        <div className="mf-uploader__upload" onClick={this.upload}>
          <Icon type={this.state.loading ? 'loading' : 'plus'} />
        </div>)}
        <input type="file" ref={this.input} style={{ display: "none" } } onChange={this.uploading} />
      </div>
    )
  }
}
