import React, { ReactElement } from 'react';
import realImage from "../../assets/image/icn_renyuanshenhe.png";
import "./index.less";

class CertHeader extends React.Component<{}> {
  public render() {
    return (
      <div className="mf-cert-header">
        <div className="mf-cert-header__title">申请人实名认证</div>
        <div className="mf-cert-header__wrap">
          <img src={realImage} alt=""/>
          <span className="mf-cert-header__desc">
              您已在手机上完成了个人实名认证
          </span>
        </div>
      </div>
    )
  }
};

export default CertHeader;
