import React, { createRef, ChangeEvent } from "react";
import "./index.less";
import { Upload, Icon, Select } from "antd";
import data from "./city.json";

const { Option } = Select;
const cityData: any = data
const provinceData = Object.keys(cityData);

export default class CityPicker extends React.Component<{}, {
  provinceData: string[],
  cities: string[],
  county: string[],
  address: string[],
}> {
  constructor(props: {}) {
    super(props);
    this.state = {
      provinceData: provinceData,
      cities: [],
      county: [],
      address: [],
    }
  }
  handleProvinceChange = (value: any) => {
    // 1
    const city = cityData[value];
    const cities = Object.keys(city);
    this.setState({
      address: [value],
      cities: cities,
    });
  }

  onCityChange = (value: any) => {
    // 2
    const province: string = this.state.address[0];
    if (province) {
      const county: string[] = cityData[province][value];
      let tempAddress = JSON.parse(JSON.stringify(this.state.address));
      if (tempAddress.length === 1) {
        tempAddress.push(value);
      } else if (tempAddress.length < 3) {
        tempAddress[1] = value;
      } else {
        tempAddress[1] = value;
        tempAddress = tempAddress.splice(0, 2);
      }
      this.setState({
        address: tempAddress,
        county,
      })
      setTimeout(() => {
      }, 1000);
    }
  }
  onCountyChange = (value: any) => {
    // 3
    const tempAddress = JSON.parse(JSON.stringify(this.state.address));
    if (tempAddress.length > 2) {
      tempAddress[2] = value;
    } else {
      tempAddress.push(value);
    }
    this.setState({
      address: tempAddress,
    });
  }
  render() {
    return (<div>
      <Select
          style={{ width: 120 }}
          onChange={this.handleProvinceChange}
          defaultValue="请选择省份"
        >
          {this.state.provinceData.map(province => (
            <Option key={province}>{province}</Option>
          ))}
      </Select>
      <Select
        style={{ width: 120, marginLeft: "12px" }}
        value={this.state.address.length > 1 ? this.state.address[1] : "请选择城市" }
        onChange={this.onCityChange}
      >
        {this.state.cities.map(city => (
          <Option key={city}>{city}</Option>
        ))}
      </Select>
      <Select
        style={{ width: 120, marginLeft: "12px" }}
        value={this.state.address.length > 2 ? this.state.address[2] : "请选择区/县" }
        onChange={this.onCountyChange}
      >
        {this.state.county.map(county => (
          <Option key={county}>{county}</Option>
        ))}
      </Select>
    </div>)
  }
}
